<template>
  <div
    @mouseover="moveButton"
    @mouseleave="clearButtonPosition"
    class="orderButton"
    ref="orderButton"
  >
    <button ref="button" class="orderButton__button">
      Заказать <br />
      уборку
    </button>
  </div>
</template>

<script>
import gsap from "gsap";

export default {
  name: "TheOrderButton",

  methods: {
    rotateButton() {
      const tl = gsap.timeline();

      tl.to(this.$refs.button, {
        rotate: 360,
        duration: 10,
        ease: "none",
        repeat: -1,
      });

      return tl;
    },
    moveButton() {
      gsap.set(this.$refs.button, { xPercent: -50, yPercent: -50 });

      const order = this.$refs.orderButton;
      const orderLeft = order.getBoundingClientRect().left;
      const orderTop = order.getBoundingClientRect().top;
      const ball = this.$refs.button;
      const pos = { x: window.innerWidth / 2, y: window.innerHeight / 2 };
      const mouse = { x: pos.x, y: pos.y };
      const speed = 0.35;

      const xSet = gsap.quickSetter(ball, "x", "px");
      const ySet = gsap.quickSetter(ball, "y", "px");

      this.$el.addEventListener("mousemove", (e) => {
        mouse.x = e.x;
        mouse.y = e.y;
      });

      gsap.ticker.add(() => {
        // adjust speed for higher refresh monitors
        const dt = 1.0 - Math.pow(1.0 - speed, gsap.ticker.deltaRatio());

        pos.x += (mouse.x - pos.x - orderLeft) * dt;
        pos.y += (mouse.y - pos.y - orderTop) * dt;
        xSet(pos.x);
        ySet(pos.y);
      });
    },

    clearButtonPosition() {
      const tl = gsap.timeline();
      const ball = this.$refs.button;

      console.log(ball);
      tl.to(ball, { x: 0, y: 0 });
      return tl;
    },
    setHide({ hide }) {
      const tl = gsap.timeline();
      const order = this.$refs.orderButton;

      tl.to(order, { scale: hide ? 0 : 1, y: hide ? "100%" : 0 });
      return tl;
    },
  },

  mounted() {
    this.rotateButton();
    this.emitter.on("hide", this.setHide);
  },
};
</script>

<style lang="scss" scoped>
.orderButton {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
  width: 30vw;
  height: 300px;
  left: calc(50% - 15vw);

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    background-color: $purple;
    border-radius: 50%;
    width: 195px;
    height: 195px;
    border: none;
    color: $light;
    font: 500 18px/23.4px $fontMedium;
  }
}
</style>
